import React, { useEffect, useState } from 'react';
import Layout from '../Layout/Layout';
import * as d3 from 'd3';
import classes from './D3Layout.module.css';
import Loader from 'react-loader-spinner';

const API_BASE_PATH = "https://ppa-coinmarketcap.herokuapp.com/coinmarketcap/";
const API_MARKETCAP_BASE_URI = "listings/latest";
const START_PARAM = "start";
const LIMIT_PARAM = "limit";
const CONVERT_PARAM = "convert";
const CRYPTOS_OLD = ["BTC", "ETH", "XRP", "USDT", "BCH", "BSV", "LTC", "EOS", "BNB", "OTHER"];
const CRYPTOS = ["BTC", "ETH", "XRP", "OTHERS"];



function D3Layout(props) {
    const getMarketCapURI = (start, limit, convert) => (
        `${API_BASE_PATH}${API_MARKETCAP_BASE_URI}?${START_PARAM}=${start}&${LIMIT_PARAM}=${limit}&${CONVERT_PARAM}=${convert}`
    );

    const getMarketCap = (start, limit, convert) => {
        let market = {
            totalMarketCap: 0,
            cryptosMarketCap: 0,
            coins: []
        }

        market = d3.json(getMarketCapURI(start, limit, convert))
            .then(function (data) {
                let res = {
                    totalMarketCap: 0,
                    cryptosMarketCap: 0,
                    coins: []
                }
                let i = 0;
                if (data.data && data.data instanceof Object) {
                    for (let coin in data.data) {
                        //console.log(data.data[coin]);
                        res.totalMarketCap = res.totalMarketCap + data.data[coin].quote.USD.market_cap;
                        if (CRYPTOS.find((symbol) => data.data[coin].symbol === symbol)) {
                            res.cryptosMarketCap = res.cryptosMarketCap + data.data[coin].quote.USD.market_cap;
                            res.coins[i] = { name: data.data[coin].symbol, value: data.data[coin].quote.USD.market_cap };
                        }
                        i++;
                    }
                }

                // manage loader
                setActiveLoader(false);
                setActivePieBlock(true);
                return res;
            });
        return market;
    };

    const getPcOfMarketcap = (value, marketCap) => {
        return ((value * 100) / marketCap).toFixed(2);
    }

    const [activeLoader, setActiveLoader] = useState(true);
    const [activePieBlock, setActivePieBlock] = useState(false);

    const getD3 = async () => {
        // size
        const margin = { top: 10, right: 10, bottom: 10, left: 10 };
        const width = 500 - margin.left - margin.right;
        const height = 500 - margin.top - margin.bottom;
        console.log(width + margin.left + margin.right);

        let market = {};
        // get data from coinmarketcap
        market = await getMarketCap(1, 5000, 'USD');

        // data object processed by d3
        let data = [];
        // add selected coins
        for (let coin in market.coins) {
            data[coin] = { name: market.coins[coin].name, value: getPcOfMarketcap(market.coins[coin].value, market.totalMarketCap) };
        }
        // add others
        data[data.length] = { name: 'OTHERS', value: getPcOfMarketcap(market.totalMarketCap - market.cryptosMarketCap, market.totalMarketCap) };
        console.log(data);

        // select svg and set attributes
        const svg = d3.select(`svg.${classes.PieMarketCapSvg}`)
            .attr("viewBox", [-width / 2, -height / 2, width, height]);

        // set colors
        const color = d3.scaleOrdinal()
            .domain(data.map(d => d.name))
            .range(d3.quantize(t => d3.interpolateSpectral(t * 0.8 + 0.1), data.length).reverse());

        const arc = d3.arc()
            .innerRadius(0)
            .outerRadius(Math.min(width, height) / 2 - 1);

        const radius = Math.min(width, height) / 2 * 0.8;
        const arcLabel = d3.arc().innerRadius(0).outerRadius(radius);

        // set pie
        let pie = d3.pie()
            .sort(null)
            .value(d => d.value);

        const arcs = pie(data);

        svg.append("g")
            .attr("stroke", "white")
            .selectAll("path")
            .data(arcs)
            .join("path")
            .attr("fill", d => color(d.data.name))
            .attr("d", arc)
            .append("title")
            .text(d => `${d.data.name}: ${d.data.value.toLocaleString()}`);

        svg.append("g")
            .attr("font-family", "sans-serif")
            .attr("font-size", 12)
            .attr("text-anchor", "middle")
            .selectAll("text")
            .data(arcs)
            .join("text")
            .attr("transform", d => `translate(${arcLabel.centroid(d)})`)
            .call(text => text.append("tspan")
                .attr("y", "-0.4em")
                .attr("font-weight", "bold")
                .text(d => d.data.name))
            .call(text => text.filter(d => (d.endAngle - d.startAngle) > 0.25).append("tspan")
                .attr("x", 0)
                .attr("y", "0.7em")
                .attr("fill-opacity", 0.7)
                .text(d => d.data.value.toLocaleString()));

        // const radius = Math.min(width, height) / 2;
        // const g = svg.append("g").attr("transform", "translate(" + width / 2 + "," + height / 2 + ")");

        // const color = d3.scaleOrdinal(['#ff7f00', '#7459ff', '#59ffce', '#ff4a11', '#7cff11', '#11f0ff', '#df11ff', '#ffe611', '#ff1111', '#28ff11']);

        // // Generate the pie
        // let pie = d3.pie();

        // console.log(`radius = ${radius}`);

        // // Generate the arcs
        // let arc = d3.arc()
        //     .outerRadius(radius)
        //     .innerRadius(0);

        // //Generate groups
        // let arcs = g.selectAll("arc")
        //     .data(pie(data))
        //     .enter()
        //     .append("g")
        //     .attr("class", "arc")

        // //Draw arc paths
        // arcs.append("path")
        //     .attr("fill", function (d, i) {
        //         return color(i);
        //     })
        //     .attr("d", arc);

    }

    useEffect(() => {
        getD3();
    });
    return (
        <Layout>
            {props.children}
            <Loader className={activeLoader ? classes.LoaderActive : classes.LoaderInactive}
                type="BallTriangle"
                color="rgb(0, 158, 108)"
                radius={1}
                height={200}
                width={200}
            >
            </Loader>
            <div className={classes.PieMarketCap + ' ' + (activePieBlock ? ' ' + classes.PieMarketCapActive : ' ' + classes.PieMarketCapInactive)}>
                <svg className={classes.PieMarketCapSvg}>
                </svg>
            </div>
            <div className={classes.ConentMarketCap}>
                <p>{props.content}</p>
            </div>
        </Layout >
    );
}



export default D3Layout;